import { customElement, property, query } from 'lit/decorators.js';
import { css, CSSResultGroup, LitElement, html, TemplateResult } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import './zwc-breadcrumb-item';
import '../shared/zwc-template';
import { ZwcBreadcrumbItem } from './zwc-breadcrumb-item';

@customElement('zwc-breadcrumb')
export class ZwcBreadcrumb extends LitElement {
  @query('slot')
  defaultSlot: HTMLSlotElement | undefined;

  @query('slot[name="separator"]')
  separatorSlot: HTMLSlotElement | undefined;

  @property({ type: Boolean, reflect: true })
  inverted = false;

  static override styles?: CSSResultGroup | undefined = [
    css`
      .breadcrumbs {
        display: flex;
        flex-direction: row;
        gap: 3px;
      }
    `,
  ];

  // Generates a clone of the separator element to use for each breadcrumb item
  private getSeparator() {
    const separator = this.separatorSlot?.assignedElements({
      flatten: true,
    })[0] as HTMLElement;

    // Clone it, remove ids, and slot it
    const clone = separator.cloneNode(true) as HTMLElement;
    [clone, ...clone.querySelectorAll('[id]')].forEach(el =>
      el.removeAttribute('id'),
    );
    clone.setAttribute('data-default', '');
    clone.slot = 'separator';

    return clone;
  }

  private handleSlotChange() {
    if (this.defaultSlot) {
      const items = [
        ...this.defaultSlot.assignedElements({ flatten: true }),
      ].filter(
        item => item.tagName.toLowerCase() === 'zwc-breadcrumb-item',
      ) as ZwcBreadcrumbItem[];

      items.forEach((item, index) => {
        if (index !== items.length - 1) {
          const separator = item.querySelector('[slot="separator"]');
          if (separator === null) {
            item.shadowRoot?.append(this.getSeparator());
          }
        }
      });
    }
  }

  protected override render(): TemplateResult {
    const classes = {
      breadcrumbs: true,
      inverted: this.inverted,
    };

    return html` <div class="${classMap(classes)}" aria-label="breadcrumbs">
      <slot @slotchange=${this.handleSlotChange}></slot>
      <slot name="separator" hidden aria-hidden="true">
        <zwc-icon size="xs" icon="action-arrow-right_outline"></zwc-icon>
      </slot>
    </div>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-breadcrumb': ZwcBreadcrumb;
  }
}
